'use strict'

import * as commonUtils from '../../../utils/CommonUtils'

import * as api from '../../../utils/Api'

const state = {
  userList: [],
  totalElements: 0
}

const getters = {
  getUserList: function (state) {
    return state.userList
  },

  getUserListTotalElements: function (state) {
    return state.totalElements
      ? state.totalElements
      : state.userList.length
  }

}

const actions = {

  requestUserList ({
    commit
  }, obj) {
    api.apiUserList({
      body: {
        pageNo: obj.page
          ? obj.page
          : 1,
        pageSize: obj.pageSize
          ? obj.pageSize
          : 10,
        roleId: obj.roleId ? obj.roleId : null,
        cid: obj.cid ? obj.cid : null,
        title: obj.title ? obj.title : null,
        name: obj.name ? obj.name : null, // 	老师姓名
        username: obj.username ? obj.username : null, // 用户账号
        uid: obj.uid ? obj.uid : null //
      },
      success: (res) => {
        if (res.data.code === 1) {
          commit({type: 'setUserList', userList: res.data.data, totalElements: res.data.totalElements})
        }
        obj.success
          ? obj.success(res)
          : null
      },
      error: (res) => {
        obj.error
          ? obj.error(res)
          : null
      },
      complete: (res) => {
        obj.complete
          ? obj.complete(res)
          : null
      }
    })
  },

  requestUserUpdate ({
    commit,
    dispatch
  }, obj) {
    api.apiUserUpdate({
      body: obj.body
        ? obj.body
        : null,
      success: (res) => {
        if (res.data.code === 1) {
          dispatch({type: 'requestUserList'})
        }
        obj.success
          ? obj.success(res)
          : null
      },
      error: (res) => {
        obj.error
          ? obj.error(res)
          : null
      },
      complete: (res) => {
        obj.complete
          ? obj.complete(res)
          : null
      }
    })
  },
  requestUserDelete ({
    commit,
    dispatch
  }, obj) {
    api.apiUserDelete({
      body: obj.body
        ? obj.body
        : null,
      success: (res) => {
        if (res.data.code === 1) {
          dispatch({type: 'requestUserList'})
        }
        obj.success
          ? obj.success(res)
          : null
      },
      error: (res) => {
        obj.error
          ? obj.error(res)
          : null
      },
      complete: (res) => {
        obj.complete
          ? obj.complete(res)
          : null
      }
    })
  },
  requestUserCreate ({
    commit,
    dispatch
  }, obj) {
    api.apiUserCreate({
      body: obj.body
        ? obj.body
        : null,
      success: (res) => {
        if (res.data.code === 1) {
          dispatch({type: 'requestUserList'})
        }
        obj.success
          ? obj.success(res)
          : null
      },
      error: (res) => {
        obj.error
          ? obj.error(res)
          : null
      },
      complete: (res) => {
        obj.complete
          ? obj.complete(res)
          : null
      }
    })
  }

}

const mutations = {
  setUserList (state, {userList, totalElements}) {
    state.userList = userList
    state.totalElements = totalElements
  },
  resetUserState (state) {
    state.userList = []
    state.totalElements = 0
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



